import Taro from '@tarojs/taro-h5';
import { Component } from "@tarojs/taro-h5";
import Nerv from "nervjs";
import { View } from '@tarojs/components';
import { throttle } from 'throttle-debounce';
import './index.scss';
class BackTop extends Component {
  constructor() {
    super(...arguments);
    this.state = {
      show: false
    };
    // web

    if (Taro.getEnv() == 'WEB') {
      this.componentScrollBox = document.documentElement;
    }
  }
  componentWillUnmount() {
    if (Taro.getEnv() == 'WEB') {
      window.removeEventListener("scroll", this.scrollbinding);
    }
  }
  componentDidMount() {
    if (Taro.getEnv() == 'WEB') {
      this.scrollbinding = throttle(300, this.handleScroll);
      window.addEventListener("scroll", this.scrollbinding);
    }
  }
  render() {
    const { show } = this.state;
    return <View>
        {show ? <View className="iconfont icon-top" onClick={this.goTop}>
            
          </View> : ""}
      </View>;
  }
  handleScroll = () => {
    const scrollTop = this.componentScrollBox.scrollTop;
    const show = scrollTop >= 0.5 * document.body.clientHeight;
    this.setState({
      show: show
    });
  };
  onPageScroll = e => {
    const scrollTop = e.scrollTop;
    const show = scrollTop > 500 ? true : false;
    this.setState({ show: show });
  };
  goTop = () => {
    if (Taro.getEnv() == 'WEB') {
      this.componentScrollBox.scrollTop = 0;
    } else if (Taro.getEnv() == 'WEAPP') {
      Taro.pageScrollTo({
        scrollTop: 0
      });
    }
  };
}
export default BackTop;